@import url(https://cdnjs.cloudflare.com/ajax/libs/meyer-reset/2.0/reset.min.css);
html * {
  font-family: 'Trebuchet MS' !important;
  font-size: 18px;
}

body {
  background-color: rgb(235, 235, 235);
}

input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.signInWithGoogleButton {
  background-color: red;
  color: white;
  width: 80%;
  font-weight: 500;
  height: 36px;
  border: none;
  border-radius: 6px;
  margin-top: 12px;
  margin-bottom: 16px;
}

.greyLargeText {
  font-size: 18px;
  font-weight: 550;
  color: #8e8e8e;
  display: inline-block;
  width: 80%;
  padding-bottom: 12px;
}

.greySmallText {
  padding-top: 16px;
  font-size: 14px;
  font-weight: 400;
  color: #8e8e8e;
  display: inline-block;
  width: 80%;
  line-height: 1.4;
  padding-bottom: 24px;
}


.authInput {
  width: 73%;
  padding: 12px;
  border: 1px solid #A9A9A9;
  background-color: rgb(250, 250, 250);
  margin: 4px;
  border-radius: 4px;
  font-size: 14px;
  color: black;
}

.authContainer {
  background-color: white;
  border: 1px solid rgb(220, 220, 220);
  text-align: center;
  flex-direction: column; 
  width: 375px;
  padding-top: 40px;
  margin: 0 auto;
  margin-top: 36px;
}

@media (max-width: 400px) {
  /* hide border on mobile view */
  .authContainer {
    padding-top: 0px;
    border: 0px;
  }
}

.errorText {
  display: inline-block;
  width: 80%;
  text-align: center;
  color: red;
}

.postContainer {
  background-color: white;
  border: 1px solid rgb(220, 220, 220);
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 360px;
  margin-top: 12px;
  margin-bottom: 24px;
}

.middleInfo {
  padding-left: 16px;
  padding-top: 16px;
}

.icon {
  margin-right: 4px;
}

.numLikes {
  padding-top: 4px;
}

.deletePostButton {
  float: right;
}

.caption {
  padding-left: 16px;
  padding-top: 8px;
  padding-bottom: 8px;
  font-size: 16px;
}

.numLikes {
  font-weight: 300;
  font-size: 16px;
}

.creatorName {
  font-weight: 600;
  display: inline-block;

}

.location {
  font-weight: 300;
  font-size: 14px;
  width: 100%;
  display: inline-block;
}

.topInfoText {
  display: inline-block;
}
.topInfo {
  padding: 12px;
  padding-bottom: 6px;
  /* margin: auto; */
}

.profileImageSmallCropper {
  width: 32px;
  height: 32px;
  /* position: relative; */
  overflow: hidden;
  border-radius: 50%;
  margin-right: 8px;
  float: left;

}

.profilePictureSmall {
  /* display: inline; */
  height: 100%;
  width: auto;
}


.commentInput {
  /* margin-top: 0; */
  border:none;
  padding-left: 16px;
  vertical-align: middle;
}

.addCommentDiv {
  border-top: 1px solid #E0E0E0;
  height: 40px;
  line-height: 40px;
  margin-top: 8px;
}

.comment {
  font-size: 16px;
  padding-left: 16px;
  padding-bottom: 4px;
}
.commentInput:focus {
  outline:none;
}

.commentCreatorName {
  font-size: 16px;
  font-weight: 600;
}

.commentText {
  font-size: 16px;
}
.commentSubmit {
  float: right;
  margin-right: 12px;
  color: #0080FF;
}
.postFactoryContainer {
  background-color: white;
  border: 1px solid rgb(220, 220, 220);
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 360px;
  margin-top: 24px;

}

.postButton {
  margin-right: 4%;
  background-color: red;
  color: white;
  width: 80px;
  font-weight: 500;
  height: 36px;
  border: none;
  float: right;
  border-radius: 6px;
  margin-right: 32px;
  margin-bottom: 12px;
}

.postFactoryText {
  width: 30%;
  padding-left: 24px;
  padding-top: 24px;
}

.postFactoryInput {
  margin-left: 24px;
  width: 80%;
  margin-top: 12px;
  padding-left: 12px;
  border: 1px solid grey;
  height: 32px;
  background-color: rgb(250, 250, 250);
  border-radius: 4px;
  font-size: 12px;
  color: black;
}

.buttonContainer {
  padding-bottom: 12px;
}

.postFactoryTitle {
  margin-left: 24px;
  margin-top: 24px;
  font-size: 20px;
  font-weight: bold;
}

.fileinput {
  margin-left: 24px;
  width: 276px;
  margin-bottom: 16px;
}
.profileContainer {
  background-color: white;
  border: 1px solid rgb(220, 220, 220);
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 360px;
  margin-top: 24px;
}

.changePictureSpan {
  padding-top: 32px;
  padding-left: 24px;
  padding-bottom: 0px;
}

.logOutSpan {
  padding-left: 24px;
  padding-top: 24px;
  padding-bottom: 24px;
}

.submitSpan {
  padding-left: 24px;
  padding-top: 12px;
}

.nameInput {
  width: 40%;
  margin-left: 24px;
  margin-top: 12px;
  padding-left: 12px;
  border: 1px solid grey;
  height: 28px;
  background-color: rgb(250, 250, 250);
  border-radius: 4px;
  font-size: 14px;
  color: black;
}

.fileinput {
  padding-top: 12px;
  padding-bottom: 0;
}
.profileImageCropper {
  width: 160px;
  height: 160px;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  margin: 0 auto;
  margin-bottom: 24px;
}
.profilePicture {
  display: inline;
  margin: 0 auto;
  height: 100%;
  width: auto;
}
.profileButton {
  background-color: red;
  color: white;
  width: 80px;
  font-weight: 500;
  height: 28px;
  border: none;
  border-radius: 4px;
  align-items: center;
  margin-left: 24px;
  margin-top: 4px;
}

.title {
  margin-left: 24px;
  margin-top: 24px;
  margin-bottom: 8px;
  font-size: 20px;
  font-weight: bold;
}

.navigationBar {
  height: 64px;
  width: 100%;
  background-color: white;
  border: 1px solid rgb(220, 220, 220);
}

.li {
  display: inline;
}

.icons {
  width: 32px;
  height: 32px;
  padding-left: 16px;
}


.contentsLeft {
  padding-left: 16px;
  float: left;
  transform: translateY(10%);
}

.contentsRight {
  padding-right: 16px;
  float: right;
  transform: translateY(50%);
}
